.widgets-php {
	
	[data-reach-dialog-overlay] {
		display: flex;
		align-items: center;
		left: var(--modal-left, 160px);
		background: rgba(245, 247, 249, 0.7);
	}


	[data-reach-dialog-content] {
		position: relative;
		width: 550px;
		// min-height: 300px;
		font-size: 14px;
		margin: auto;
		border-radius: 6px;
		box-shadow: 0 50px 300px rgba(92, 110, 128, 0.5);

		.close-button {
			display: flex;
			align-items: center;
			justify-content: center;
			position: absolute;
			top: 5px;
			right: 5px;
			width: 40px;
			height: 40px;
			font-size: 20px;
			color: #717a84;
			line-height: 0;
			padding: 0;
			appearance: none;
			border: none;
			cursor: pointer;
			background: transparent;
			-webkit-font-smoothing: antialiased;
			-moz-osx-font-smoothing: grayscale;
			opacity: 0.4;
			transition: opacity 0.15s ease;

			&:hover {
				opacity: 1;
			}

			&:focus {
				outline: none;
			}
		}
	}

	.ct-sidebar-conditions {
		display: flex;
		flex-direction: column;
		padding: 20px;
		min-height: 270px;

		h1 {
			font-size: 1.5em;
			line-height: 1.5;
			margin: 0 0 20px 0;
		}

		p {
			font-size: inherit;
			margin: 0 0 20px 0;
		}

		.ct-control {
			margin-bottom: 20px;
		}
	}
}

.ct-custom-sidebar-actions {
	margin-top: auto;
	padding-top: 20px;
	border-top: 1px dashed #eee;
		
		.button-primary {
			width: 100%;
			min-height: 42px;
			font-size: 14px;
			font-weight: 500;
		}
}