.ct-sidebars-manager {
  padding-top: 12px; }
  @media (max-width: 1249px) {
    .ct-sidebars-manager {
      max-width: 450px; } }
  @media (min-width: 1250px) {
    .ct-sidebars-manager {
      max-width: calc(900px + 2%); } }
  .ct-sidebars-manager h2 {
    margin: 0;
    padding: 10px 0; }
  .ct-sidebars-manager p {
    margin: 0 0 15px 0;
    color: #555d66; }
  .ct-sidebars-manager form {
    display: flex;
    flex-wrap: wrap;
    padding: 15px;
    margin-bottom: 20px;
    background: #fff;
    border: 1px solid #ccd0d4;
    box-sizing: border-box;
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.04); }
    .ct-sidebars-manager form input {
      flex: 1;
      margin-right: 15px; }

#widgets-right .widgets-holder-wrap {
  height: auto; }
  #widgets-right .widgets-holder-wrap .widgets-sortables .sidebar-name {
    margin: 0 -8px 10px -8px;
    border-bottom: 1px solid #e5e5e5; }
    #widgets-right .widgets-holder-wrap .widgets-sortables .sidebar-name h2 {
      padding-left: 15px; }
    #widgets-right .widgets-holder-wrap .widgets-sortables .sidebar-name button {
      width: 48px;
      height: 48px;
      margin: 0;
      border-left: 1px solid #e5e5e5; }
  #widgets-right .widgets-holder-wrap.closed {
    height: 48px; }

[id*="ct-dynamic-sidebar"] .blocksy-settings,
[id*="ct-dynamic-sidebar"] .sidebar-description {
  position: absolute;
  top: 0;
  opacity: 0.8;
  width: 48px;
  height: 48px;
  cursor: pointer;
  margin-top: 0 !important;
  border-left: 1px solid #e5e5e5;
  font-size: 0 !important; }
  [id*="ct-dynamic-sidebar"] .blocksy-settings .ct-tooltip-top,
  [id*="ct-dynamic-sidebar"] .sidebar-description .ct-tooltip-top {
    height: 30px;
    color: #fff;
    margin: 0;
    padding: 0 10px !important;
    transform: translate3d(0px, -45px, 0px); }
  [id*="ct-dynamic-sidebar"] .blocksy-settings:hover,
  [id*="ct-dynamic-sidebar"] .sidebar-description:hover {
    opacity: 1; }
    [id*="ct-dynamic-sidebar"] .blocksy-settings:hover .ct-tooltip-top,
    [id*="ct-dynamic-sidebar"] .sidebar-description:hover .ct-tooltip-top {
      opacity: 1;
      visibility: visible;
      transform: translate3d(0px, -50px, 0px); }

[id*="ct-dynamic-sidebar"] .blocksy-settings {
  right: 97px; }
  [id*="ct-dynamic-sidebar"] .blocksy-settings span {
    display: flex;
    align-items: center;
    justify-content: center;
    width: inherit;
    height: inherit;
    position: relative; }
    [id*="ct-dynamic-sidebar"] .blocksy-settings span:before {
      font-family: dashicons;
      content: "\f111";
      font-size: 15px; }

[id*="ct-dynamic-sidebar"] .sidebar-description {
  display: flex !important;
  align-items: center;
  justify-content: center;
  right: 48px; }
  [id*="ct-dynamic-sidebar"] .sidebar-description:before {
    font-family: dashicons;
    content: '\f335';
    font-size: 18px; }
  [id*="ct-dynamic-sidebar"] .sidebar-description p:not(.ct-tooltip-top) {
    display: none; }
  [id*="ct-dynamic-sidebar"] .sidebar-description:hover:before {
    color: #dd4b39; }

.widgets-php [data-reach-dialog-overlay] {
  display: flex;
  align-items: center;
  left: var(--modal-left, 160px);
  background: rgba(245, 247, 249, 0.7); }

.widgets-php [data-reach-dialog-content] {
  position: relative;
  width: 550px;
  font-size: 14px;
  margin: auto;
  border-radius: 6px;
  box-shadow: 0 50px 300px rgba(92, 110, 128, 0.5); }
  .widgets-php [data-reach-dialog-content] .close-button {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 5px;
    right: 5px;
    width: 40px;
    height: 40px;
    font-size: 20px;
    color: #717a84;
    line-height: 0;
    padding: 0;
    appearance: none;
    border: none;
    cursor: pointer;
    background: transparent;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    opacity: 0.4;
    transition: opacity 0.15s ease; }
    .widgets-php [data-reach-dialog-content] .close-button:hover {
      opacity: 1; }
    .widgets-php [data-reach-dialog-content] .close-button:focus {
      outline: none; }

.widgets-php .ct-sidebar-conditions {
  display: flex;
  flex-direction: column;
  padding: 20px;
  min-height: 270px; }
  .widgets-php .ct-sidebar-conditions h1 {
    font-size: 1.5em;
    line-height: 1.5;
    margin: 0 0 20px 0; }
  .widgets-php .ct-sidebar-conditions p {
    font-size: inherit;
    margin: 0 0 20px 0; }
  .widgets-php .ct-sidebar-conditions .ct-control {
    margin-bottom: 20px; }

.ct-custom-sidebar-actions {
  margin-top: auto;
  padding-top: 20px;
  border-top: 1px dashed #eee; }
  .ct-custom-sidebar-actions .button-primary {
    width: 100%;
    min-height: 42px;
    font-size: 14px;
    font-weight: 500; }
